body,
html,
#root {
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

#bg {
  background-image: url(./img/Imagebackground.svg);
  height: 100%;
  width: 100%;
  background-position: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.App {
  position: absolute;
  text-align: center;
  min-width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  top: 0px;
}

#pageTitle {
  font-size: 80px;
  text-align: center;
  font-size: 3rem;
  margin-top: 0;
  padding-top: 2rem;
  text-shadow: 0 0 7px rgba(255, 255, 255, 0.4),
    0 0 10px rgba(255, 255, 255, 0.803), 0 0 21px rgba(255, 255, 255, 0.816),
    0 0 42px #0fa, 0 0 151px #0fa;
}

#counterInterface {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.4rem;
  display: flex;
  flex-direction: column;
  padding-top: 9rem;
  justify-self: center;
}

#interfaceTitle {
  font-size: 1.4rem;
  margin: 0;
}

#cunt {
  padding-top: 1.5rem;
  margin: 0;
  font-size: 3rem;
  text-shadow: rgb(18, 232, 160);
}

button {
  width: 5rem;
  height: 30px;
  font-size: 1.2rem;
  color: white;
  background-color: transparent;
  font-family: 'Orbitron', sans-serif;
  font-family: 'Teko', sans-serif;
}

#buttons {
  display: flex;
  justify-content: center;
  gap: 25px;
}
